.hero__description {
    padding-bottom: 20px;
    color: #FFFFFF;
    font-family: 'Source Code Pro', monospace;
    font-size: 1em;
    line-height: 1.75;
    font-weight: 400;
}

.hero__buttons {

    padding-bottom: 236px;

    @include tablet {
        padding-bottom: 12px;
    }

    @include desctop {
        padding-bottom: 42px;
    }
}

.products-btn {
    border: 2px solid #f8d24e;
    border-radius: 8px;
    padding: 11px 26px;

    color: #000000;

    font-weight: 600;
    font-size: 12px;
    line-height: calc(15 / 12);

    &:hover, &:focus {
        border: 2px solid #0A8817;
    }
    &:disabled {
        border: 2px solid #ABABAB;
    }
}

.advantages-btn {
    border-radius: 8px;
    margin-left: 12px;

    color: #f8d24e;

    font-weight: 600;
    font-size: 12px;
    line-height: calc(15 / 12);
    text-decoration-line: underline;

    &:hover {
        color: #0A8817;
    }

    &:focus {
        color: #FCFCFC;
    }

    &:disabled {
        color: #ABABAB;
    }
}


