.label-h2 {
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: calc(22 / 18);
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #131313;
  margin-bottom: 12px;

  @media screen and (min-width:768px) and (max-width:1199px) {
    font-size: 22px;
    line-height: calc(27 / 22);
  }

  @media screen and (min-width:1200px) {
    font-size: 30px;
    line-height: calc(37 / 30);
  }
}

.product-h2 {
  font-family: 'Seymour One', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: calc(38 / 30);
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #131313;
  text-align: center;
  margin-bottom: 148px;

  @media screen and (min-width:768px) and (max-width:1199px) {
    font-size: 34px;
    line-height: 43px;
    margin-bottom: 243px;
  }

  @media screen and (min-width:1200px) {
    margin-bottom: 287px;
    font-size: 36px;
    line-height: calc(45 / 36);
    letter-spacing: 0.06em;
  }
}

.product-box {
  display: flex;
  flex-wrap: wrap;

}

.product-item {
  position: relative;
  border-radius: 24px;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 123px 40px 16px 40px;
  height: 420px;

  &:not(:last-child) {
    margin-bottom: 168px;
  }

  @media screen and (min-width:480px) and (max-width:767px) {
    height: 350px;
  }

  @media screen and (min-width:768px) and (max-width:1199px) {
    width: calc(1/3*100% - (1 - 1/3)*16px);
    height: 428px;
    padding: 108px 26px 12px 26px;
    margin-bottom: 0;

    &:not(:last-child) {
      margin-right: 16px;
      margin-bottom: 0;
    }
  }

  @media screen and (min-width:1200px) {
    width: calc(1/3*100% - (1 - 1/3)*29px);
    padding: 165px 64px 60px 64px;
    height: 536px;


    &:not(:last-child) {
      margin-right: 29px;
      margin-bottom: 0;
    }
  }
}

