// -----Modal Window------

.backdrop {
  display: none;
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  transition: opacity 250ms ease, visibility 250s ease;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 280px;
  max-height: 90%;
  background: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 28px 20px;
  overflow-y: scroll;

  @media screen and (min-width: 768px) {
    width: 568px;
    padding: 32px 40px;
  }
}

.modal-btn-close {
  position: absolute;
  right: 12px;
  top: 12px;
}

.modal-title {
  font-family: 'Seymour One';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: calc(28 / 20);
  color: #131313;
  margin-bottom: 20px;
  text-align: center;

  @media screen and (min-width: 768px) {
    margin-bottom: 24px;
  }
}

.modal-img-box {
  width: 132px;
  margin: 0 auto 22px;
  padding-bottom: 7px;
  border-radius: 50%;
  background: linear-gradient(0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)),
    #d9d9d9;
  box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);

  @media screen and (min-width: 768px) {
    margin-bottom: 24px;
  }
}

.consist {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;

  @media screen and (min-width: 768px) {
    margin-bottom: 48px;
  }
}

.consist-item {
  width: calc(1 / 4 * 100% - (1 - 1 / 3) * 9px);
  border: 2px solid #9cbe8f;
  border-radius: 40px;

  &:not(:last-child) {
    margin-right: 9px;
  }

  @media screen and (min-width: 768px) {
    width: 65px;
  }
}

.consist-number {
  font-weight: 500;
  font-size: 10px;
  line-height: calc(28 / 10);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #131313;
  background: #9cbe8f;
  border: 2px solid #9cbe8f;
  border-radius: 50%;
  box-shadow: 0px 6px 20px rgba(10, 136, 23, 0.25);
  width: 43px;
  height: 43px;
  margin: 0 auto 8px auto;

  @media screen and (min-width: 768px) {
    font-size: 12px;
    line-height: calc(28 / 12);
    height: 53px;
    width: 53px;
  }
}

.consist-name {
  font-style: normal;
  text-align: center;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #131313;
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    font-size: 12px;
    line-height: 15px;
    margin-bottom: 14px;
  }
}

.ingredients {
  margin-bottom: 40px;
  border: none;
  width: 100%;

  & caption {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: calc(28 / 20);
    text-align: center;
    color: #131313;
    margin-bottom: 24px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 48px;
  }
