*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

p, table, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul, ol, li {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
  display: inline-block;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  background: none;
  border: none;
}

.container {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 60px 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    padding: 60px 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 60px 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 60px 16px;
  }
}

body {
  background: #132e65;
  font-family: Source Code Pro, monospace;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.text--uppercase {
  text-transform: uppercase;
}

.text--white {
  color: #fcfcfc;
}

.container-fluid {
  max-width: 100%;
  padding-left: 16px;
  padding-right: 16px;
}

.itc-slider {
  position: relative;
}

.itc-slider__wrapper {
  border-radius: 8px;
  overflow: hidden;
}

.itc-slider__items {
  will-change: transform;
  transition: transform .5s;
  display: flex;
}

.itc-slider__transition-none {
  transition: none;
}

.itc-slider__item {
  max-width: 100%;
  -ms-user-select: none;
  user-select: none;
  will-change: transform;
  flex: 0 0 100%;
}

.itc-slider__btn {
  width: 40px;
  height: 50px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background: rgba(0, 0, 0, .2);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.itc-slider__btn_hide {
  display: none;
}

.itc-slider__btn_prev {
  left: 0;
}

.itc-slider__btn_next {
  right: 0;
}

.itc-slider__btn:hover, .itc-slider__btn:focus {
  color: #fff;
  background: rgba(0, 0, 0, .3);
  outline: 0;
  text-decoration: none;
}

.itc-slider__btn:before {
  content: "";
  width: 20px;
  height: 20px;
  background: center / 100% 100% no-repeat;
  display: inline-block;
}

.itc-slider__btn_prev:before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}

.itc-slider__btn_next:before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}

.itc-slider__indicators {
  z-index: 15;
  justify-content: center;
  margin: 0 15%;
  padding-left: 0;
  list-style: none;
  display: flex;
  position: absolute;
  bottom: -24px;
  left: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .itc-slider__indicators {
    bottom: -44px;
  }
}

.itc-slider__indicator {
  box-sizing: content-box;
  width: 12px;
  height: 12px;
  text-indent: -999px;
  cursor: pointer;
  background-color: #d9d9d9;
  background-clip: padding-box;
  border-radius: 50%;
  flex: 0 auto;
  margin-left: 3px;
  margin-right: 3px;
}

.itc-slider__indicator_active {
  background-color: #84a178;
}

svg {
  display: block;
}

@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-2vw);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.inner {
  animation: slideInFromLeft 1s ease-out forwards;
  position: relative;
  top: 10vh;
}

