.hero__placing {
  width: 1200px;
  padding: 126px 90px;
}

.hero__title {
  color: #fff;
  padding-bottom: 32px;
  font-family: Source Code Pro, monospace;
  font-size: 2.75em;
  font-weight: 400;
  line-height: 1.25;
}

.hero__description {
  color: #fff;
  padding-bottom: 20px;
  font-family: Source Code Pro, monospace;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.75;
}

.hero__buttons {
  padding-bottom: 236px;
}

@media screen and (min-width: 768px) {
  .hero__buttons {
    padding-bottom: 12px;
  }
}

@media screen and (min-width: 1200px) {
  .hero__buttons {
    padding-bottom: 42px;
  }
}

.products-btn {
  color: #000;
  border: 2px solid #f8d24e;
  border-radius: 8px;
  padding: 11px 26px;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
}

.products-btn:hover, .products-btn:focus {
  border: 2px solid #0a8817;
}

.products-btn:disabled {
  border: 2px solid #ababab;
}

.advantages-btn {
  color: #f8d24e;
  border-radius: 8px;
  margin-left: 12px;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
  text-decoration-line: underline;
}

.advantages-btn:hover {
  color: #0a8817;
}

.advantages-btn:focus {
  color: #fcfcfc;
}

.advantages-btn:disabled {
  color: #ababab;
}

.product {
  background: #0e2a47;
}

.label-h2 {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #131313;
  margin-bottom: 12px;
  font-size: 18px;
  font-style: normal;
  font-weight: 800;
  line-height: 1.22222;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .label-h2 {
